import React, { PureComponent } from 'react'
import ReactMarkdown from 'react-markdown'
import {Helmet} from "react-helmet"
import LocationIcon from '../images/location.svg'

export default class Events extends PureComponent {
    componentDidMount () {
        window.scrollTo(0,0)
    }

    generateEventDetail = (event, idx, isEn) =>
        <div className='event-container' key={event.location}>
            <h3 className='subject'>{ isEn ? event.subject : event.subjectCN }</h3>
            <div className='event-detail'>
                <p className='location'>
                    <img className='location-icon' src={LocationIcon} alt='location'/>
                    { isEn ? event.location : event.locationCN }
                </p>
                <ReactMarkdown
                    className='introduction'
                    children={ isEn ? event.introduction : event.introductionCN}
                    // components={{link: this.LinkRenderer}}
                    linkTarget={"_blank"}
                    rel="noopener noreferrer"
                />
                {event.videoLink &&
                    <div>
                        <div className='video-container'>
                            <div dangerouslySetInnerHTML={{__html: event.videoLink}}/>
                        </div>
                    </div>
                }
            </div>
        </div>
        
    // LinkRenderer = (props) => <a href={props.href} target="_blank" rel="noopener noreferrer">{props.children}</a>

    render () {
        const { seoTitle, seoTitleCN, items } = this.props.events
        const isEn = this.props.lang==="en"
        const allEventsDetails = items.map((event, idx) => this.generateEventDetail(event, idx, isEn))
        const seoList = items.map(event => isEn ? event.introduction : event.introductionCN)
        return (
            <div id='maincontent-container'>
                <main id='events'>
                    <Helmet>
                        <title>{ isEn ? seoTitle : seoTitleCN }</title>
                        <meta name="description" content={ seoList.join(" ") } />
                    </Helmet>
                    <h2>{ isEn ? "Some Events" : "部分学术活动"}</h2>
                    {allEventsDetails}
                </main>
            </div>
        )
    }
}